import React from 'react';
import AltynTyiyn from './AltynTyiyn/AltynTyiyn';
import {sectorAltynTyiyn} from './AltynTyiyn/AltynTyiynData';
import ChyngysAitmatov from './ChyngysAitmatov/ChyngysAitmatov';
import {sectorChyngysAitmatov} from './ChyngysAitmatov/ChyngysAitmatovData';
import Philharmonic from './Philharmonic/Philharmonic';
import {sectorPhilharmonic} from './Philharmonic/PhilharmonicData';
import PhilharmonicSmall from './PhilharmonicSmall/PhilharmonicSmall';
import {sectorPhilharmonicSmall} from './PhilharmonicSmall/PhilharmonicSmallData';
import Seitek from './Seitek/Seitek';
import {sectorSeitek} from './Seitek/SeitekData';
import Maldybaeva from './Maldybaeva/Maldybaeva';
import {sectorMaldybaeva} from './Maldybaeva/MaldybaevaData';
import Shapito from './Shapito/Shapito';
import {sectorShapito} from './Shapito/ShapitoData';
import Circus from './Circus/Circus';
import {sectorCircus} from './Circus/CircusData';
import Spartak from './Spartak/Spartak';
import {sectorSpartak} from './Spartak/SpartakData';
import Sport from './Sport/Sport';
import {sectorSport} from './Sport/SportData';
import OshDrama from './OshDrama/OshDrama';
import {sectorOshDrama} from './OshDrama/OshDramaData';
import Barpy from './Barpy/Barpy';
import {sectorBarpy} from './Barpy/BarpyData';
import History from './History/History';
import {sectorHistory} from './History/HistoryData';
import Dram from './Dram/Dram';
import {sectorDram} from './Dram/DramData';
import OshPhilharmonic from './OshPhilharmonic/OshPhilharmonic';

export const getWhere = async (name, sector, setWhereData, prices, selectDate, cashbox, addSeats, _event) => {
    if(name==='АлтынТуйун')
        return <AltynTyiyn sector={sector} setWhereData={setWhereData} prices={prices} cashbox={cashbox} selectDate={selectDate} addSeats={addSeats} _event={_event}/>;
    else if(name==='Русский драм театр им. Ч.Айтматова')
        return <ChyngysAitmatov sector={sector} setWhereData={setWhereData} prices={prices} cashbox={cashbox} selectDate={selectDate} addSeats={addSeats} _event={_event}/>;
    else if(name==='Ош Филармония им. Р. Абдыкадырова')
        return <OshPhilharmonic sector={sector} setWhereData={setWhereData} prices={prices} cashbox={cashbox} selectDate={selectDate} addSeats={addSeats} _event={_event}/>;
    else if(name==='Кыргызская Государственная Филармония')
        return <Philharmonic sector={sector} setWhereData={setWhereData} prices={prices} cashbox={cashbox} selectDate={selectDate} addSeats={addSeats} _event={_event}/>;
    else if(name==='Кыргызская Государственная Филармония - Малый зал')
        return <PhilharmonicSmall sector={sector} setWhereData={setWhereData} prices={prices} cashbox={cashbox} selectDate={selectDate} addSeats={addSeats} _event={_event}/>;
    else if(name==='Национальный Центр детей и юношества "Сейтек"')
        return <Seitek sector={sector} setWhereData={setWhereData} prices={prices} cashbox={cashbox} selectDate={selectDate} addSeats={addSeats} _event={_event}/>;
    else if(name==='Киргизский национальный академический театр оперы и балета им. Абдыласа Малдыбаева')
        return <Maldybaeva sector={sector} setWhereData={setWhereData} prices={prices} cashbox={cashbox} selectDate={selectDate} addSeats={addSeats} _event={_event}/>;
    else if(name==='Цирк Шапито')
        return <Shapito sector={sector} setWhereData={setWhereData} prices={prices} cashbox={cashbox} selectDate={selectDate} addSeats={addSeats} _event={_event}/>;
    else if(name==='Кыргызский государственный цирк им. А. Изибаева')
        return <Circus sector={sector} setWhereData={setWhereData} prices={prices} cashbox={cashbox} selectDate={selectDate} addSeats={addSeats} _event={_event}/>;
    else if(name==='Стадион Спартак имени Долона Омурзакова')
        return <Spartak sector={sector} setWhereData={setWhereData} prices={prices} cashbox={cashbox} selectDate={selectDate} addSeats={addSeats} _event={_event}/>;
    else if(name==='Дворец спорта им. К. Кожомкула')
        return <Sport sector={sector} setWhereData={setWhereData} prices={prices} cashbox={cashbox} selectDate={selectDate} addSeats={addSeats} _event={_event}/>;
    else if(name==='Ош улуттук драма театры')
        return <OshDrama sector={sector} setWhereData={setWhereData} prices={prices} cashbox={cashbox} selectDate={selectDate} addSeats={addSeats} _event={_event}/>;
    else if(name==='Барыпы атындагы кыргыз драма театры')
        return <Barpy sector={sector} setWhereData={setWhereData} prices={prices} cashbox={cashbox} selectDate={selectDate} addSeats={addSeats} _event={_event}/>;
    else if(name==='Киргизский государственный исторический музей')
        return <History sector={sector} setWhereData={setWhereData} prices={prices} cashbox={cashbox} selectDate={selectDate} addSeats={addSeats} _event={_event}/>;
    else if(name==='Кыргызский драм. театр им. Т. Абдумомунова')
        return <Dram sector={sector} setWhereData={setWhereData} prices={prices} cashbox={cashbox} selectDate={selectDate} addSeats={addSeats} _event={_event}/>;
    else if(name==='')
        return null;
}

export const getWhereDara = async (name) => {if(name==='АлтынТуйун')
        return sectorAltynTyiyn();
    else if(name==='Русский драм театр им. Ч.Айтматова')
        return sectorChyngysAitmatov();
    else if(name==='Кыргызская Государственная Филармония')
        return sectorPhilharmonic();
    else if(name==='Кыргызская Государственная Филармония - Малый зал')
        return sectorPhilharmonicSmall();
    else if(name==='Киргизский национальный академический театр оперы и балета им. Абдыласа Малдыбаева')
        return sectorMaldybaeva();
    else if(name==='Национальный Центр детей и юношества "Сейтек"')
        return sectorSeitek();
    else if(name==='Цирк Шапито')
        return sectorShapito();
    else if(name==='Кыргызский государственный цирк им. А. Изибаева')
        return sectorCircus();
    else if(name==='Стадион Спартак имени Долона Омурзакова')
        return sectorSpartak();
    else if(name==='Дворец спорта им. К. Кожомкула')
        return sectorSport();
    else if(name==='Ош улуттук драма театры')
        return sectorOshDrama()
    else if(name==='Барыпы атындагы кыргыз драма театры')
        return sectorBarpy()
    else if(name==='Киргизский государственный исторический музей')
        return sectorHistory()
    else if(name==='Кыргызский драм. театр им. Т. Абдумомунова')
        return sectorDram()
    else if(name==='')
        return null;
}