import React, { useState } from 'react';
import './Part.css';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import MenuItem from '@material-ui/core/MenuItem';
import { withStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import Tooltip from '@material-ui/core/Tooltip';
import IconButton from '@material-ui/core/IconButton';
import DeleteIcon from '@material-ui/icons/Delete';
import ArrowLeft from '@material-ui/icons/ArrowLeft';
import ArrowRight from '@material-ui/icons/ArrowRight';

import { mainWindow } from '../../../App'

const width1 = mainWindow.current.offsetWidth>800? 240: 120;
const styles = theme => ({
    button: {
        margin: theme.spacing.unit,
    },
    textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: 500,
    },
    urls: {
        margin: theme.spacing.unit,
        width: 500,
        maxHeight: 100,
        overflow: 'auto'
    },
    error_message: {
        marginTop: theme.spacing.unit,
        marginBottom: theme.spacing.unit,
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        color: 'red',
        fontWeight: 'bold'
    },
    textFieldSmall: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: width1
    },
    menu: {
        width: 200,
    }
});

const CutImage  = (
    (props)=>{
        let i;
        let {seats, price, setWhereData, selectSector, selectDate, cashbox, addSeats, _event} = props;
        let [show, setShow] = useState(false);
        let statuses = ['block', 'free', 'sold']
        let [status, setStatus] = useState('');
        let handleStatus = (event) => {
            setStatus(event.target.value)
        };
        let [selectedPrice, setSelectedPrice] = useState({});
        let [_count, set_count] = useState('');
        let handle_count = (event) => {
            set_count(event.target.value)
        };
        let [_price, set_price] = useState('');
        let handle_price = (event) => {
            if(cashbox&&seats[selectDate].without!==undefined){
                console.log(seats[selectDate][selectSector], selectSector)
                for(let i=0; i<seats[selectDate][selectSector].length; i++){
                    if(seats[selectDate][selectSector][i].price.price===event.target.value.price){
                        setSelectedPrice(i)
                        set_price(event.target.value)
                        set_count(0)

                    }
                }
            } else if(!cashbox){
                set_price(event.target.value)
                set_count(0)
            }
        };
        let [count, setCount] = useState('0');
        let handleCount = (event) => {
            setCount(event.target.value)
        };
        let [selectIndex, setSelectIndex] = useState([0,0]);
        const { classes } = props;
        return (
            <>
            {seats[selectDate]!==undefined?
                seats[selectDate].without===undefined?
                    seats[selectDate][selectSector]!==undefined?
                        <div style={{width: ((seats[selectDate][selectSector][0].length)*26+150)+'px'}}>
                        {price!==undefined&&price.length>0?
                            <div className='part-div-price' style={{width: ((seats[selectDate][selectSector][0].length)*40+100)+'px'}}>
                                <div className='part-number'/>
                                <div className='part-empty' style={{background: 'red'}}/>
                                <div className='part-price'>
                                    {'Продано'}
                                </div>
                                {
                                    price.map((element) => {
                                        return(
                                            <>
                                            <div className='part-empty' style={{background: element.color}}/>
                                            <div className='part-price'>
                                                {element.price + ' сом'}
                                            </div>
                                            </>
                                        )
                                    })
                                }
                            </div>
                            :
                            null}
                        {seats[selectDate][selectSector].map((row, rowId) => {
                            i=1;
                            for(let i1 = 0; i1 < row.length; i1++){
                                if(row[i1].type!=='empty')
                                    i+=1
                            }
                            return (
                                <div>
                                    <div className='part-number'>
                                        {'Ряд: ' + (row[0].name.split(':')[0].split(' ')[1])}
                                    </div>
                                    {
                                        row.map((place, placeId) => {
                                            if (place.type === 'seat') {
                                                i-=1;
                                                return (
                                                    <Tooltip title={place.color!=='red'?'Места: '+seats[selectDate][selectSector][rowId][placeId].name+(place.color!=='gray'?' Цена: '+seats[selectDate][selectSector][rowId][placeId].price:''):'Продано'}>
                                                        <div onClick={() => {
                                                            console.log(cashbox&&place.status !== 'block'&&place.status !== 'sold')
                                                            if(!cashbox){
                                                                setSelectIndex([rowId, placeId]);
                                                                setStatus(place.status);
                                                                set_price({price: place.price, color: place.color});
                                                                setShow(true)
                                                            } else if(cashbox&&place.status !== 'block'&&place.status !== 'sold') {
                                                                if(place.color!=='red') {
                                                                    seats[selectDate][selectSector][rowId][placeId].color = 'red'
                                                                    seats[selectDate][selectSector][rowId][placeId].status = 'hold'
                                                                } else {
                                                                    seats[selectDate][selectSector][rowId][placeId].status = 'free'
                                                                    for(let i = 0; i< price.length; i++){
                                                                        if(price[i].price===place.price)
                                                                            seats[selectDate][selectSector][rowId][placeId].color = price[i].color
                                                                    }
                                                                }
                                                                _event.where.data = seats;
                                                                setWhereData({target: {value: _event}})
                                                                addSeats([place, selectDate])
                                                            }
                                                        }} className='part' style={{background: place.color}}>
                                                            <div className='part-text'>
                                                                {place.name.split(':')[1].split(' ')[0]}
                                                            </div>
                                                        </div>
                                                    </Tooltip>
                                                )
                                            } else
                                                return (
                                                    <div className='part-empty' style={{background: place.color}}/>
                                                )
                                        })
                                    }
                                </div>
                            )
                        })}
                    </div>
                        :
                        null
                    :
                    seats[selectDate][selectSector]!==undefined?
                        cashbox?
                            <div>
                            <div>
                                <TextField
                                    select
                                    SelectProps={{
                                        MenuProps: {
                                            className: classes.menu,
                                        },
                                    }}
                                    label='цена'
                                    type='login'
                                    className={classes.textFieldSmall}
                                    margin='normal'
                                    value={_price}
                                    onChange={handle_price}
                                >
                                    {price !== undefined?
                                        price.map(option => (
                                            <MenuItem key={option.price} value={option}>
                                                {option.price}
                                            </MenuItem>
                                        ))
                                        :
                                        null
                                    }
                                </TextField>
                                {_price.price!=undefined?
                                    <>
                                    <IconButton className={classes.button} onClick={ async()=>{
                                        let count = _count
                                        console.log('-', count!==0, count)
                                        if(count!==0)
                                            set_count(--count)
                                    }} aria-label='Delete'><ArrowLeft /></IconButton>
                                    {_count}
                                    <IconButton className={classes.button} onClick={ async()=>{
                                        let count = _count
                                        if(parseInt(seats[selectDate][selectSector][selectedPrice].free)!==parseInt(count))
                                            set_count(++count)
                                    }} aria-label='Delete'><ArrowRight /></IconButton>
                                    {'Свободно: '+seats[selectDate][selectSector][selectedPrice].free}
                                    </>
                                    :
                                    null
                                }
                            </div>
                            {_price.price!=undefined?
                                <Button variant='contained' color='primary' onClick={async ()=>{
                                    if(parseInt(_count)>0){
                                        seats[selectDate][selectSector][selectedPrice].free=parseInt(seats[selectDate][selectSector][selectedPrice].free)-parseInt(_count)
                                        seats[selectDate][selectSector][selectedPrice].sold=_count
                                        addSeats([{name: _count+' мест в '+selectSector, price: parseInt(_price.price)*parseInt(_count)}, selectDate])
                                        _event.where.data = seats;
                                        await setWhereData({target: {value: _event}});
                                        await set_count('');
                                        await set_price('');
                                    }
                                }} className={classes.button}>
                                    Добавить место
                                </Button>
                            :
                            null
                            }
                        </div>
                            :
                            <div>
                            {seats[selectDate][selectSector].length>0?
                                seats[selectDate][selectSector].map((seat, seatId) => {
                                    return (
                                        <div>
                                        {'Совободно: '+seat.free+' Куплено: '+seat.sold+' Цена: '+seat.price.price}
                                        {_event===-1?
                                            <IconButton className={classes.button} onClick={ async()=>{
                                                let _seats = seats[selectDate][selectSector];
                                                set_count('');
                                                set_price('');
                                                _seats.splice(seatId, 1);
                                                await setWhereData(seats);
                                            }} aria-label='Delete'><DeleteIcon /></IconButton>
                                            :
                                            null
                                        }
                                        </div>
                                    )
                                }):null
                            }
                            <br/>
                            <TextField
                                label='количество'
                                type='login'
                                className={classes.textFieldSmall}
                                margin='normal'
                                value={_count}
                                onChange={handle_count}
                            />
                            <TextField
                                select
                                SelectProps={{
                                    MenuProps: {
                                        className: classes.menu,
                                    },
                                }}
                                label='цена'
                                type='login'
                                className={classes.textFieldSmall}
                                margin='normal'
                                value={_price}
                                onChange={handle_price}
                            >
                                {price !== undefined?
                                    price.map(option => (
                                        <MenuItem key={option.price} value={option}>
                                            {option.price}
                                        </MenuItem>
                                    ))
                                    :
                                    null
                                }
                            </TextField>
                            <br/>
                            <Button variant='contained' color='primary' onClick={async ()=>{
                                let _seats = seats[selectDate][selectSector];
                                console.log(_seats)
                                let has = false;
                                for(let i=0; i<_seats.length; i++){
                                    has = _seats[i].price === _price
                                }
                                if(!has){
                                    _seats.push({free: _count, sold: 0, price: _price})
                                    await setWhereData(seats);
                                }
                                set_count('')
                                set_price('')
                            }} className={classes.button}>
                                Добавить место
                            </Button>
                        </div>
                        :
                        null
                :
                null
            }
            <Dialog
                open={show}
                onClose={()=>{setShow(false)}}
                aria-labelledby='alert-dialog-title'
                aria-describedby='alert-dialog-description'>
                <DialogContent>
                    <TextField
                        select
                        SelectProps={{
                            MenuProps: {
                                className: classes.menu,
                            },
                        }}
                        label='статус'
                        type='login'
                        className={classes.textField}
                        margin='normal'
                        value={status}
                        onChange={handleStatus}
                    >
                        {statuses != undefined?
                            statuses.map(option => (
                                <MenuItem key={option} value={option}>
                                    {option}
                                </MenuItem>
                            ))
                            :
                            null
                        }
                    </TextField>
                    <br/>
                    {status==='free'?
                        <>
                        <TextField
                            select
                            SelectProps={{
                                MenuProps: {
                                    className: classes.menu,
                                },
                            }}
                            label='цена'
                            type='login'
                            className={classes.textField}
                            margin='normal'
                            value={_price}
                            onChange={handle_price}
                        >
                            {price != undefined?
                                price.map(option => (
                                    <MenuItem key={option.price} value={option}>
                                        {option.price}
                                    </MenuItem>
                                ))
                                :
                                null
                            }
                        </TextField>
                        <br/>
                        </>
                        :
                        null
                    }
                    <div>
                        <Button variant='contained' color='primary' onClick={()=>{
                            setShow(false);
                            if(status==='block'){
                                seats[selectDate][selectSector][selectIndex[0]][selectIndex[1]].color='Gray'
                                seats[selectDate][selectSector][selectIndex[0]][selectIndex[1]].price='0';
                                seats[selectDate][selectSector][selectIndex[0]][selectIndex[1]].status=status;
                            } else if(status==='sold'){
                                seats[selectDate][selectSector][selectIndex[0]][selectIndex[1]].color='Red'
                                seats[selectDate][selectSector][selectIndex[0]][selectIndex[1]].status=status;
                                seats[selectDate][selectSector][selectIndex[0]][selectIndex[1]].price='0';
                            } else if(_price.price!==undefined){
                                seats[selectDate][selectSector][selectIndex[0]][selectIndex[1]].status=status;
                                seats[selectDate][selectSector][selectIndex[0]][selectIndex[1]].price=_price.price;
                                seats[selectDate][selectSector][selectIndex[0]][selectIndex[1]].color=_price.color;
                            }
                            setWhereData(seats)
                            console.log(seats)
                        }} className={classes.button}>
                            Сохранить
                        </Button>
                        <Button variant='contained' color='secondary' onClick={()=>{setShow(false)}} className={classes.button}>
                            Отмена
                        </Button>
                    </div>
                </DialogContent>
            </Dialog>
            </>
        )
    }
)

CutImage.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(CutImage);
